/* ==========================================================================
   In this file we store styles for the splash screen
   ========================================================================== */

/* Container
   ========================================================================== */

#welcome-container {
    position: absolute;
    width: 100%;
    display: flex;
    height: 100%;
    background: rgba(255, 200, 200, 0.5);
    visibility: hidden;
}

#splashTextContainer {
    overflow-y: auto;
    max-height: 400px;
    padding: 0px 13px 0px 13px;
}

/* Welcome text
   ========================================================================== */

.welcome-message {
    margin: 60px auto auto auto;
    padding: 20px 30px 20px 30px;
    background: rgba(255, 255, 255, 0.7);
    border-radius: 30px;
    text-align: center;
    width: 330px;
    border: 5px solid white;
    vertical-align: top;
}

.info {
    text-align: left;
    margin-top: 5px;
    margin-bottom: 10px;
}

.infoPoint {
    text-align: left;
    margin: 0.3em 0 0 0;
    font-size: 0.9em;
}

#made-by {
    font-size: 13px;
    margin: 20px 0 0 0;
    opacity: 0.4;
}

.faq-link {
    font-size: 15px;
    margin: 20px 0 0 0;
    opacity: 1;
    cursor: pointer;
    text-align: center;
}

.donate {
    position: relative;
    padding: 1em 0;
    margin: 1em 0;
    &__title {
        font-size: 0.9em;
        opacity: 0.7;
        margin-top: 0;
    }
    &__list {
        display: flex;
        align-items: center;
        justify-content: center;
        a {
            margin: 0 0.4em;
        }
    }
    &:before,
    &:after {
        content: "";
        position: absolute;
        width: 263px;
        height: 1px;
        background: hsl(0, 0%, 77%);
        transform: translate(-50%);
        opacity: 0.5;
    }
    &:before {
        top: 0;
    }
    &:after {
        bottom: 0;
    }
}

.flattr-logo {
    height: 1em;
    text-decoration: none;
}

/* Image
   ========================================================================== */

.logoImageWelcome {
    margin-bottom: 4px;
}

#logo {
    position: absolute;
    bottom: 35px;
    right: 20px;
    /*opacity: 0.5;*/
    pointer-events: none;
}

/* Form
   ========================================================================== */

#start-form {
    margin-top: 0.25em;
}

.usernameinput {
    height: 30px;
    width: 170px;
    color: #222;
    font-weight: bold;
    background: rgba(255, 255, 255, 0.8);
    margin-top: 0.5em;
    /*text-align: center;*/
}